.pro-img {
    width: 200px;
}

.profile-img {
    gap: 20px;
}

.profile-img p {
    width: 290px;
}

.btn-secondary {
    width: 100%;
}
.content {
    justify-content: space-between;
}
.bar {
    position: relative;
    max-width: 500px;
    width: 100%;
    background: transparent;
    margin: 0 15px;
    padding: 10px 20px;
    border-radius: 7px;
}

.bar .skill-box {
    width: 100%;
    margin: 25px 0;
}

.skill-box .title {
    display: block;
    font-size: 14px;
    font-weight: 600;
    color: white;
}

.skill-box .skill-bar {
    height: 8px;
    width: 100%;
    border-radius: 6px;
    margin-top: 6px;
    background: rgba(0,0,0,0.1);
}
.skill-bar .skill-per {
    position: relative;
    display: block;
    height: 100%;
    width: 90%;
    border-radius: 6px;
    background: #4070f4;
    animation: progress 0.4s ease-in-out forwards;
    opacity: 0;
}

.skill-per.html {
    width: 50%;
    animation-delay: 0.1s;
}

.skill-per.css {
    width: 70%;
    animation-delay: 0.1s;
}

.skill-per.javascript {
    width: 50%;
    animation-delay: 0.2s;
}

.skill-per.nodejs {
    width: 30%;
    animation-delay: 0.3s;
}

@keyframes progress {
    0% {
    width: 0;
    opacity: 1;
    }

    100% {
    opacity: 1;
    }
}

.skill-per .tooltip {
    position: absolute;
    right: -14px;
    top: -28px;
    font-size: 9px;
    font-weight: 500;
    color: #fff;
    padding: 2px 6px;
    border-radius: 3px;
    background: #4070f4;
    z-index: 1;
    }

.tooltip::before {
    content: '';
    position: absolute;
    left: 50%;
    bottom: -2px;
    height: 10px;
    width: 10px;
    z-index: -1;
    background-color: #4070f4;
    transform: translateX(-50%) rotate(45deg);
}

.edit {
    position: absolute;
    background-color: rgba(0, 0, 0, 0.496);
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: none;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 20px;
}

.edit input {
    width: 300px;
    height: 40px;
    border: none;
    outline: none;
    border-radius: 5px;
    background-color: rgb(0, 0, 0);
    color: white;
    padding: 0 10px;
}

.must {
    color: red;
    position: absolute;
    top: 60%;
    display: none;
}

.social {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.links {
    display: flex;
    gap: 20px;
    background-color: #212529;
    padding: 15px;
    border-radius: 16px;
}

.links svg {
    font-size: 25px;
}
button {
    cursor: pointer;
}
@media (max-width: 767px) {
    .profile-img {
        align-items: center !important;
    }
    .profile-img img{
        width: 120px;
    }
    .content {
        flex-direction: column;
    }
}