.main {
    background: url(../../imgs/Python-good-for-machine-learning.jpg) no-repeat;
    background-size: cover;
    height: 500px;
    width: 100%;
    border-radius: 23px;
    padding: 50px;
    display: flex;
    align-items: center;
}
.text {
    max-width: 500px;
    display: flex;
    justify-content: flex-start;
}
.btn-primary , p {
    align-self: flex-start;
}
.text h1 {
    font-size: 50px;
}
.text span {
    color: var(--primary-color);
    font-size: 50px;
}
@media(max-width: 767px){
    .text h1 {
        font-size: 33px;
    }
    .text {
        width: 100% ;
    }
    .main {
        height: 400px;
    }
    }