h1 {
    color: white;
    font-weight: 700;
    margin-bottom: 30px;
}
.most {
    background-color: #212529;
    margin-top: 50px;
    display: grid;
    gap: 20px;
}
.row {
    row-gap: 20px;
}
.card {
    color: white;
    background-color: #27292a;
    padding: 30px;
    border-radius: 12px;
    cursor: pointer;
}
.stars {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 15px;
}

.rate , .views {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.rate div , .views div {
    font-size: 16px;
}
.rate svg {
    color: gold;
    margin-left: 5px;
    font-size: 16px;
}
.views div {
    display: flex;
    align-items: center;
}

.views svg {
    margin-left: 5px;
}

.btn-info {
    color: white;
    font-weight: bold;
}

h4 {
    font-size: 18px;
}
@media(max-width: 1999px){
    h4 {
        font-size: 14px;
    }
    
}