.library {
        background-color: #212529;
        margin-top: 50px;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        gap: 20px;
}

h1 {
    color: white;
    font-weight: 700;
    margin-bottom: 30px;
}
.img {
    height: fit-content !important;
    padding: 10px;
}

._100px {
    width: 100px;
}

.row-library{
    align-items: center;
    justify-content: space-around;
    border-bottom: 1px solid gray;
    flex-wrap: wrap;
    padding-bottom: 15px;
}
.text {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

@media (max-width: 555px) {
    ._100px {
        width: 70px;
    }
    .text-white-50{
        font-size: 14px;
    }
    .watch-btn{
        width: 70px;
        font-size: 16px;
    }
    .row-library{
        gap: 10px;
    }
}